










































import Vue from 'vue';
import Component from 'vue-class-component';

@Component({
    props: {
        total: {
            type: Number,
            default: 0
        },
        currentPage: {
            type: Number,
            default: 1
        },
        handlePage: {
            type: Function,
            default() {}
        }
    }
})
export default class Pagination extends Vue {
    changePage(num: number) {
        this.$emit('handlePage', num);
    }
};
