
















































import Vue from 'vue';
import Component from 'vue-class-component';
import { fromNow } from '../../filters/filters';
import Pagination from '../../components/Pagination.vue';

interface Options {
    page: number;
    tab: string;
    limit: number;
    mdrender: boolean;
}

@Component({
    components: {
        Pagination,
    },
    filters: {
      fromNow,
    }
})
export default class Home extends Vue {
    currentTag: string = 'all';
    total: number = 589;
    currentPage: number = 1;
    get topics(): object{
        return this.$store.state.home.topics
    }
    created(): void{
        let options: Options = {
            page: 1,
            tab: 'all',
            limit: 40,
            mdrender: true,
        };
        this.fetchTopics(options);
    }
    async fetchTopics(options: Options) {
        await this.$store.dispatch('getTopics', options);
        this.changeSider();
    }
    changeTag(tag: string): void {
        let total: number = 0;
        switch(tag) {
            case 'all':
                total = 589;
                break;
            case 'good':
                total = 16;
                break;
            case 'share':
                total = 36;
                break;
            case 'ask':
                total = 64;
                break;
            case 'job':
                total = 11;
                break;
            case 'dev':
                total = 30;
                break;
            default:
                break;
        }
        this.currentTag = tag;
        this.total = total;
        this.currentPage = 1;
        let options = {
            page: 1,
            tab: tag,
            limit: 40,
            mdrender: true,
        };
        this.fetchTopics(options);
    }
    handlePage(page: number): void {
        if (page !== this.currentPage){
            let options = {
                page,
                tab: this.currentTag,
                limit: 40,
                mdrender: true,
            };
            this.fetchTopics(options);
            window.scrollTo(0, 0);
            this.currentPage = page;
        }
    }
    async changeSider () {
        const accesstoken = window.localStorage.getItem('accesstoken');
        await this.$store.dispatch('accesstoken', {accesstoken});
        const accessInfo = this.$store.state.app.accessInfo;
        let showInfo = accessInfo.success ? true : false;
        this.$store.dispatch('authorOrNot', {
            showInfo,
            isAuthor: false,
        });
        if (accessInfo && accessInfo.loginname !== '' && accessInfo.loginname) {
            this.$store.dispatch('getInfo', {
                username: accessInfo.loginname
            });
        }
    }
};
